.Report {
  margin-left: auto;
  margin-right: auto;
  width: 62%;
  text-align: left;
  padding-bottom: 30px;
  scroll-behavior: smooth;
}
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
nav {
  position: fixed;
  top: 74px;
  left: 5px;
  text-align: left;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  border-radius: 5px;
  width: 17%;
}

nav:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.5);
}

.navLink {
  display: block;
  line-height: 20pt;
  text-decoration: none;
}

.navLink:hover {
  font-weight: bold;
  cursor: pointer;
}

nav h4,
.navLink {
  color: whitesmoke;
  padding-left: 5px;
}

h3 {
  font-size: 42pt;
  font-weight: normal;
  margin-top: 2%;
  margin-bottom: 0;
}

h4 {
  font-size: 24pt;
  margin-top: 5px;
  margin-bottom: 5px;
  font-weight: normal;
}

h5 {
  font-size: 1em;
  font-style: italic;
  padding-left: 1em;
}

p {
  text-indent: 1em;
}

em {
  padding-bottom: 5px;
}

@media only screen and (max-width: 1024px) {
  nav {
    visibility: hidden;
  }
}

@media only screen and (max-width: 720px) {
  .Report {
    width: 90%;
  }
}
